let shoucang = [];

function sclist(state=shoucang,action){
    if(action.type=='ADD_TODO'){
        console.log('reducer',action);
        return [...state,action.title];
    }
    if(action.type=='DEL_TODO'){
        let arr = [...state];
        arr.splice(action.index,1);
        return arr;
    }
    return state;
}
export default sclist;