const Home = (props) => {
  useEffect(() => {
    props.dispatch(actionCreator())
  }, [])
  
  //console.log(props.datalist)
  //render() {
    return(
        <div className="homeall">
            <div className="search">
              <i className='iconfont icon-taobao4'></i>   
              <button onClick={()=>props.history.push('/search')}>
                <i className='iconfont icon-sousuokuang'></i>寻找宝贝店铺</button>
            </div>
            <Slider />
          <div className="like">
            <span>—— </span>
            <i className='iconfont icon-xin1'></i>
            <span> </span>
            你可能还喜欢
            <span> ——</span>  
          </div>
          <div className='image'>
            {           
              props.datalist.map((item,index) => {
                return (
                  <div className="list">
                    <button onClick={()=>props.history.push('/goods',{id: props.datalist[index].id,img:props.datalist[index].img})}>
                      <img key={index} src={props.datalist[index].img} />
                      <p>{props.datalist[index].title}</p>
                      <p className="price">￥{props.datalist[index].price}<span> 1397人已购买</span></p>
                    </button>
                  </div>
                )
             })
            }
          </div>          
          <div className="bot"></div>
          <Mytab />
        </div>
      );
  //  }
  }

  const mapStateToProps = (state) => ({
    datalist: state.home.datalist
})
export default connect(mapStateToProps)(Home)
