import React from 'react'
import { Carousel } from 'antd-mobile';
import './home.css'

class Slider extends React.Component {
constructor(props){
    super(props);
    this.state = {
      data: ['1', '2', '3','4', '5'],
      imgHeight: 176,
    }
  };
    componentDidMount() {
      // simulate img loading
      setTimeout(() => {
        this.setState({
          data: ["https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg", 
          "https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg", 
          "https://gw.alicdn.com/imgextra/i4/191/O1CN01k3oS9i1DHWHMwyzaH_!!191-0-lubanu.jpg",
          "https://gw.alicdn.com/imgextra/i4/79/O1CN01xCsBma1CSDv73k0rz_!!79-0-lubanu.jpg",
          "https://gw.alicdn.com/imgextra/i3/189/O1CN0101BLCD1DGbUJJRfeY_!!189-0-lubanu.jpg",
          "https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i2/6000000007999/O1CN01p1MVOa28xalDbCnhY_!!6000000007999-0-octopus.jpg",
          "https://gw.alicdn.com/imgextra/i4/191/O1CN01k3oS9i1DHWHMwyzaH_!!191-0-lubanu.jpg",
          "https://gw.alicdn.com/imgextra/i1/123/O1CN01hzfrBq1CmNLdUT99q_!!123-0-lubanu.jpg",
          ],
        });
      }, 100);
    }
    render() {
    //   const { pathname } = this.props.location;
        return (
            <div className="carousel">
              <Carousel 
                autoplay={true}
                infinite

              >
              {this.state.data.map(val => (
                <a
                  key={val}
                  href="http://www.alipay.com"
                  style={{ display: 'inline-block', width: '100%', height:this.state.imgHeight}}
                >
                <img
                  src={`${val}`}
                  alt=""
                  style={{ width: '100%' }}
                  onLoad={() => {
                    // fire window resize event to change height
                    window.dispatchEvent(new Event('resize'));
                    this.setState({ imgHeight: 'auto' });
                  }}
                />
              </a>
            ))}
            </Carousel>
          </div>
        )
    }
}

export default Slider;