

const actionCreator = () => {
    return (dispatch)=>{
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        })
        .then(res => res.json())
        .then(res => {
            console.log(res.data)
            dispatch({
                type:'DATA',
                datalist: res.data
            })
        })
    }
    // return{
    //     type:'LOGIN',
    //     username:username

    // }
}

export {actionCreator}  //命名导出


