import React,{useEffect} from 'react'
import './goods.css';
import {actionCreator} from './actionCreator'
import {connect} from 'react-redux';

//命名导出引入的时候要加大括号
// class Goods extends React.Component {
//   constructor(props){
//     super(props);
//     this.state = {
//       value: '',
//     };
//   }

const Goods = (props) => {
  useEffect(() => {
    props.dispatch(actionCreator())
  }, [])

  //props.dispatch(loginCreator())
  //console.log(props.userinfo)
  //render() {
    return (
      <div id="all">
        {
          props.datalist.map((item,idx) => {
            //console.log(props.location.state.id)
            if(props.datalist[idx].id == props.location.state.id) {
              return (
                <div className="goods">
                  <img key={idx} src={props.datalist[idx].img} />
                  <div className="back">
                    <button onClick={()=>props.history.push('/home')}><i className='iconfont icon-fanhui'></i></button>
                    <i className='iconfont icon-jiarugouwuche'></i>
                  </div>
                  <p className="dol">￥{props.datalist[idx].price}</p>
                  <p className="text">{props.datalist[idx].title}</p>
              </div>
              )
            }
          })
        }
        <div className="bottom">
          <div className="left">
            <i className='iconfont icon-dianpuweixuanzhong-'></i>
            <i className='iconfont icon-taobaokefu-01'></i>
            <i id="shoucang" className='iconfont icon-shoucang' onClick={ () => {
              console.log(props.userinfo)
              if(props.userinfo === ''){
                props.history.push('/login')
              }else{
                props.datalist.map((item,idx) => {
                  if(props.datalist[idx].id == props.location.state.id && document.getElementById("shoucang").className !=="iconfont icon-shoucang3") {
                    document.getElementById("shoucang").className= "iconfont icon-shoucang3"
                    props.dispatch({
                      type:"ADD_TODO",
                      id: props.datalist[idx].id,
                      title:props.datalist[idx].title
                    })
                  }
                  else if(props.datalist[idx].id == props.location.state.id && document.getElementById("shoucang").className ==="iconfont icon-shoucang3") {
                    document.getElementById("shoucang").className= "iconfont icon-shoucang"
                    props.dispatch({
                      type:"DEL_TODO",
                      id: props.datalist[idx].id,
                      title:props.datalist[idx].title
                    })
                  }
              })
          }
        }
          }></i>
            <p>店铺<span>客服</span><span>收藏</span></p>
          </div>
          <div className="right"> 
            <span id="che">加入购物车</span>
            <span id="buy">立即购买</span>
          </div>
        </div>
      </div>
  ) 
  //}
}
const mapStateToProps = (state) => ({
  datalist: state.home.datalist,
  userinfo: state.login.userinfo
})
export default connect(mapStateToProps)(Goods)