import {actionCreator} from './actionCreator'
import {loginCreator} from './loginCreator';
import {connect} from 'react-redux';
import React,{useState,useEffect} from 'react';
import './login.css'

const Login = (props) => {  
    //props.dispatch(loginCreator())

    return(
        <div className="login">
            <h1>您需要登录才能访问</h1>
            <div className="ipt"><input type="text" placeholder="手机号/邮箱/会员名" /></div>
            <div className="passwd"><input type="text" placeholder="请输入登录密码" /></div>
            <button onClick={()=> {
                // props.dispatch({
                //     type:"LOGIN",
                //     userinfo:props.userinfo
                // }) 
                props.dispatch(loginCreator())
                //console.log(props.userinfo)
                localStorage.setItem("userinfo",props.userinfo) 
                props.history.push('/')
            }}id="log">登录</button>
        </div>
    )
}


const mapStateToProps = (state) => ({
    userinfo: state.login.userinfo
  })
  export default connect(mapStateToProps)(Login)