import React,{useEffect} from 'react';
import {connect} from 'react-redux'
import {loginCreator} from './loginCreator'
const Cart = (props) => {
    // useEffect(() => {
    //     props.dispatch(loginCreator())
    //   }, '')
    if(props.userinfo === ''){
        props.history.push('/login')
    }
    return (
        <div>
            <button id="gohome" onClick={()=>props.history.push('/')}>返回首页</button>
            {
                props.shoucang.map((item,idx)=>
                <li key={idx}>{item}
                <button onClick={()=>props.dispatch({
                    type:"DEL_TODO",
                    index:idx
                })}>删除</button></li>)  
            }</div>
        )
    }
const mapStateToProps = (state) => {
    //console.log(state);
    return {
        shoucang:state.shoucang,
        userinfo: state.login.userinfo

    }
}

export default connect(mapStateToProps)(Cart);